import { FC } from "react";
import porscheImg from "./img/porsche.avif";
import "./CarCard.scss";
import Phone from "../PhoneComponents/Phone";
import useScreenResize from "../../hooks/useScreenResize";
import DesignCredit from "../Shared/DesignCredit";

type ButtonProp = {
  text: string;
  extraClass?: string;
  ping?: boolean;
  pingExtraClass?: string;
};

const Button: FC<ButtonProp> = ({ text, ping, extraClass = "", pingExtraClass = "" }) => {
  return (
    <li className="button-gradient relative m-0 flex h-8 w-8 items-center justify-center rounded-full bg-gray-500/50 bg-gradient-to-b from-gray-700/75 to-gray-500/75 p-0">
      <span className={`font-sans ${extraClass ?? ""}`}>{text}</span>
      {ping && <span className={`absolute animate-ping ${pingExtraClass}`}>{text}</span>}
    </li>
  );
};

type SubTitleProps = {
  text: string;
  extraClass?: string;
};

const SubTitle: FC<SubTitleProps> = ({ text, extraClass }) => (
  <h3
    className={`text-sm font-normal leading-none text-gray-300 ${extraClass ?? ""}`}
  >
    {text}
  </h3>
);

type CarFeatureProps = {
  label: string;
  feature: string;
};

const CarFeature: FC<CarFeatureProps> = ({ label, feature }) => (
  <div className="flex flex-1 flex-grow flex-col gap-1 rounded-xl bg-blue-900/10 px-2 py-2 backdrop-blur-3xl">
    <p className="m-0 text-[.6rem] leading-none text-gray-200">{label}</p>
    <h4 className="m-0 text-sm font-normal leading-none">{feature}</h4>
  </div>
);

const CarCard = () => {
  const screenSize = useScreenResize();

  const { height: screenHeight } = screenSize;
  const transform = screenHeight
    ? `scale(${(screenHeight / 900).toFixed(1)})`
    : "";
  // screenHeight && screenHeight < 1000
  //   ? `scale(${(screenHeight / 900).toFixed(1)})`
  //   : "scale(1)";

  return (
    <div className={`flex flex-col transition-transform`} style={{ transform }}>
      <Phone>
        <ul className="m-0 flex list-none items-center justify-between px-4 py-6 blur-[0.25px]">
          <Button extraClass={"scale-x-50 -mt-[3px] -ml-[2px]"} text="<" />
          <li>
            <SubTitle text="Selected Car" />
          </li>
          <Button text="♡" extraClass="text-sm" ping={true}  />
        </ul>
        <article>
          <header></header>
          <main>
            <h2 className="scale-x-75 text-center font-serif text-[5rem] font-normal leading-[5rem] tracking-tight">
              Porsche
              <br />
              <span className="text-[6rem]">GT3</span>
            </h2>
            <img
              className={`animate-car -mt-[80px] h-[255px] -scale-x-100 object-cover`}
              src={porscheImg}
              alt="Porsche 911 GT3 Blue"
            />
            <div className="mx-4 -mt-2 flex h-full flex-col items-center gap-3 rounded-2xl bg-gradient-to-br from-[rgba(42,81,123,0.98)] to-gray-200/30 px-2 py-4 backdrop-blur-3xl">
              <SubTitle text="Specs" extraClass="text-white font-semibold" />
              <div
                data-name="features"
                className="flex w-full justify-between gap-1"
              >
                <CarFeature label="Passenger" feature="2 seats" />
                <CarFeature label="0-100 km/h" feature="3.4 sec" />
                <CarFeature label="Max Speed" feature="319 km/h" />
              </div>
              <div
                data-name="price"
                className="mt-3 flex w-full flex-col items-center"
              >
                <SubTitle text="Price" extraClass="text-white font-semibold" />
                <div>
                  <data
                    className="inline-block scale-x-75 font-serif text-[2.5rem] leading-none"
                    value="1453"
                  >
                    $1,453
                  </data>
                  <span className="font-serif text-sm">/day</span>
                </div>
              </div>
              <button
                style={{ border: "1.5px outset rgba(255,255,255,.75)" }}
                className="mb-3 animate-pulse rounded-3xl bg-gradient-to-b from-gray-700 via-black to-gray-800 px-3 py-2 text-[.75rem]"
              >
                <span className="grayscale filter">🗓</span> Book 2 days
              </button>
            </div>
          </main>
          <footer></footer>
        </article>
      </Phone>
      <DesignCredit
      name="@dchernyshuk" link="https://x.com/dchernyshuk/status/1799056308202836334" />
    </div>
  );
};

export default CarCard;
