import { FC, ReactNode } from "react";
import Checkmark from "../Icons/Checkmark";
import clsx from "clsx";

type AnimatePresentationType = {
  orientation?: "up" | "down";
  fade?: "in" | "out";
  children: ReactNode;
};

const AnimatePresentation: FC<AnimatePresentationType> = ({
  children,
  orientation,
  fade,
}) => {
  const classes  = clsx({
    'animate-fade-in': fade === 'in' && !orientation,
    'animate-fade-out': fade === 'out' && !orientation,
    'animate-down': !fade && orientation === 'down',
    'animate-up': !fade && orientation === 'up',
    'animate-fade-in-down': fade === 'in' && orientation === 'down',
    'animate-fade-in-up': fade === 'in' && orientation === 'up',
    'animate-fade-out-down': fade === 'out' && orientation === 'down',
    'animate-fade-out-up': fade === 'out' && orientation === 'up',
  })
  return (
    <div className={classes} style={{ animationDelay: "0s", animationDuration:'1.666s',animationTimingFunction:'cubic-bezier(0,1.49,.97,.94)', padding: 0, margin:0, height: 'auto', width: 'auto', position: 'relative' }}>
      {children}
    </div>
  );
};

const RefactoringHeroUnit = () => {
  return (
    <div className="min-w-[400px] overflow-visible sm:min-w-[600px]">
      <h2 className="mx-auto px-4 text-center text-4xl font-normal leading-[1.2] tracking-tighter text-white sm:px-6 sm:text-[3.5rem] sm:leading-[4.5rem] md:max-w-2xl md:px-4 lg:max-w-6xl lg:px-10">
        Get Refactoring UI today.
      </h2>
      <p className="mx-auto mt-4 max-w-3xl px-8 text-center text-xl font-normal tracking-tight text-slate-400 sm:text-2xl">
        Learn how to design beautiful user interfaces by yourself
        <span className="w-0 transition-all sm:w-full">
          using specific tactics explained from a developer's point-of-view.
        </span>
      </p>
    </div>
  );
};

const RefactoringPriceCard = () => {
  return (
    <div className="flex w-svw flex-col items-center justify-center gap-10 px-24">
      <AnimatePresentation fade="in" orientation="down" >
        <RefactoringHeroUnit />
      </AnimatePresentation>
      <AnimatePresentation fade="in" orientation="up" > 

      <div className="z-60 block min-w-[360px] shrink rounded-2xl bg-white/10 bg-[radial-gradient(145.05%_100%_at_33%_0%,#1D2B41DD_0%,#0B1627DD_57.38%,#0B1627AA_67.38%,#142133DD_88.16%)] p-8 shadow-2xl ring-1 ring-white/25 backdrop-blur-sm sm:min-w-[448px] lg:w-1/2 lg:max-w-[30rem]">
        <div>
          <h3 className="text-lg font-semibold" property="schema:name">
            The Essentials
          </h3>
          <div
            typeof="schema:Product"
            vocab="http://schema.org/"
            className="mt-5 flex items-center gap-3"
          >
            <div
              property="schema:offers"
              typeof="schema:Offer"
              className="text-5xl font-bold"
            >
              <span property="schema:priceCurrency" content="USD">
                $
              </span>
              <span property="schema:price" content="29.99">
                99
              </span>
            </div>
            <div className="flex flex-col leading-4 text-slate-400">
              <span className="text-lg font-medium leading-6">USD</span>
              <span className="text-sm">plus local taxes</span>
            </div>
          </div>
          <button className="mt-7 h-14 w-full animate-pulse rounded-full bg-slate-700/65 text-sm ring-1 ring-white/25 hover:border-white/20 hover:bg-slate-700/60 hover:ring-2 focus:outline-none focus:ring-2 focus:ring-white focus:ring-offset-2 focus:ring-offset-slate-900">
            Buy Now
          </button>
          <div className="mt-10 flex flex-col gap-3">
            <span className="flex items-center gap-3">
              <Checkmark />
              <span>
                The <b>218-page book</b> in PDF format
              </span>
            </span>
            <span className="flex items-center gap-3">
              <Checkmark />
              <span>
                All three <b>in-depth video tutorialst</b>
              </span>
            </span>
          </div>
        </div>
      </div>
      </AnimatePresentation>
    </div>
  );
};

export default RefactoringPriceCard;
