import clsx from "clsx";
import { FC } from "react";

type AirlineLogoProps = {
  color: `bg-${string}`;
  name: string;
};

const AirlineLogo: FC<AirlineLogoProps> = ({ color, name }) => {
  const classes = clsx("w-fit rounded-sm px-2 text-white", {
    [color]: color,
  });
  return (
    <div aria-label="logo" className={classes}>
      {name}
    </div>
  );
};

export default AirlineLogo;
