import { useEffect, useState } from "react";

export type screenSizeType = {
  width?: number;
  height?: number;
};

const isSameSize = (prevSize: screenSizeType, currSize: screenSizeType) => {
  if (!prevSize) return false;
  const { width: prevWidth, height: prevHeight } = prevSize;
  const { width: currWidth, height: currHeight } = currSize;
  return prevWidth === currWidth && prevHeight === currHeight;
};

const useScreenResize = (): screenSizeType => {
  const [screenSize, setScreenSize] = useState<screenSizeType>({});

  useEffect(() => {
    const updateScreenSize = () => {
      const { innerWidth: width, innerHeight: height } = window;
      if (!isSameSize(screenSize, { width, height })) {

        setScreenSize({ width, height });
      }
    };
    updateScreenSize();
    window.addEventListener("resize", updateScreenSize);

    return () => window.removeEventListener("resize", updateScreenSize);
  }, []);

  return screenSize;
};

export default useScreenResize;
