import { FC } from "react";
import { useNavigate } from "react-router-dom";

type PageProps = {
  pageIndex: number;
  slug?: string;
  isCurrentPage: boolean;
  lastPage: number;
};

const Page: FC<PageProps> = ({ pageIndex, slug, isCurrentPage, lastPage }) => {
  const navigate = useNavigate();
  const isFirstPage = pageIndex === 1;
  const isLastPage = pageIndex === lastPage;
  const borderRadius = isFirstPage ? 'rounded-l-md' : isLastPage ? 'rounded-r-md' : '';
  return (
    <span
      className={`cursor-pointer px-3 text-sm ${isCurrentPage ? `bg-white/20 mix-blend-color-burn ${borderRadius}` : "hover:text-blue-300"}`}
      onClick={isCurrentPage ? undefined : () => navigate(`./${slug}`)}
    >
      {pageIndex}
    </span>
  );
};

export default Page;
