import { FC } from "react";
import Page from "./Page";

type PaginationProps = {
  pageCount: number;
  slugs: (string | undefined)[];
  currentPage: number;
};

const createIndices = (indexCount: number) => [...Array(indexCount).keys()];

const Pagination: FC<PaginationProps> = ({ pageCount, slugs, currentPage }) => {
  const indices = createIndices(pageCount);

  return (
    <div className="absolute top-10 z-50 flex w-full justify-center text-lg text-white bg-blend-difference select-none">
      <div className="grid grid-flow-col divide-x divide-gray-200/50">
        {indices.map((pageIndex: number) => (
          <Page
            key={pageIndex}
            pageIndex={pageIndex + 1}
            slug={slugs[pageIndex]}
            isCurrentPage={currentPage === pageIndex + 1}
            lastPage={indices.length}
          />
        ))}
      </div>
    </div>
  );
};

export default Pagination;
