import { FC, ReactNode } from "react";
import "./TravelingRay.scss";

type TravelingRaysProps = {
  children: ReactNode;
};

type TravelingLightProps = {
  angle?: number;
  top?: string;
  left?: string;
  color?: string;
  delay?: string;
  duration?: string;
};

const TravelingLight: FC<TravelingLightProps> = ({
  angle = 45,
  top = "50%",
  left = "-50%",
  color,
  delay = "0s",
  duration = "10s",
}) => {
  return (
    <div
      className=""
      style={{
        transform: `rotate(${angle}deg)`,
        position: "absolute",
        top,
        left,
      }}
    >
      <div
        className={`traveling-light to-white_0 relative z-20 h-1 w-24 rounded-r bg-gradient-to-l ${color}`}
        style={{ animationDelay: delay, animationDuration: duration }}
      ></div>
      <hr className="relative z-10 -mt-[2.75px] w-[200vw] stroke-gray-200 opacity-10" />
    </div>
  );
};

//
//   style={{backgroundImage: 'radial-gradient(100vw 100vh at 50% -40%,rgba(255,255,255,.1), rgba(0,0,0,0))'}}
//

const TravelingRays: FC<TravelingRaysProps> = ({ children }) => {
  return (
    <div className="relative h-screen w-full overflow-hidden bg-gray-950 bg-[radial-gradient(145.05%_100%_at_50%_-5%,#1D2B41_0%,#020509_47.38%,#0F1A29_98.16%)]">
      <div className="z-10">
        <TravelingLight
          key={"from-blue-600"}
          angle={45}
          top={"50%"}
          left={"-50%"}
          color={"from-blue-600"}
          duration="7s"
          delay="0s"
        />
        <TravelingLight
          key={"from-green-400"}
          angle={-210}
          top={"50%"}
          left={"-50%"}
          color={"from-green-400"}
          delay="5s"
        />
        <TravelingLight
          key={"from-pink-400"}
          angle={10}
          top={"20%"}
          left={"-30%"}
          color={"from-pink-400"}
          duration={"13s"}
        />
      </div>
      <div className="w-svh inset-0 z-20 h-svh bg-transparent flex justify-center items-center">
        {children}
      </div>
    </div>
  );
};

export default TravelingRays;
