import { FC, useEffect, useRef, useState } from "react";

type MaskPathProps = {
  size: number;
  position: number;
};

const MaskPath: FC<MaskPathProps> = ({ size, position }) => {

  const [sizePercentage, setSizePercentage] = useState<number>(0)

  const ref = useRef<SVGSVGElement>(null);
  useEffect(() => {
    if(ref.current){
      const width = ref.current.getBoundingClientRect().width;
      const percentageSize = size / width;
      setSizePercentage(percentageSize * 100)
    }

  },[ref.current, size])

  return (
    <svg ref={ref} width={'100%'} height={size}>
      <defs>
        <mask id="mask">
          <rect x={0} y={0} width={`${position}%`} height={size} fill="white" />
          {/* <circle cx={size} cy={-pixelVal/2} r={pixelVal} fill="white" /> */}
          <rect x={`${position + sizePercentage}%`} y={0} width={'100%'} height={size} fill="white" />
        </mask>
      </defs>
    </svg>
  );
};

export default MaskPath;
