import { useCallback, useEffect, useRef, useState } from "react";
import "./HueLights.scss";
import RangeSlider from "../Form/RangeSlider";
import CanvasWheel from "./CanvasWheel";
import MobileDisclaimer from "../Shared/MobileDisclaimer";

function inlineStyles(svgElement: SVGElement) {
  const elements = svgElement.querySelectorAll("*");
  elements.forEach((element) => {
    const computedStyle = window.getComputedStyle(element);
    const style = Array.from(computedStyle).reduce((styleString, property) => {
      return `${styleString}${property}:${computedStyle.getPropertyValue(property)};`;
    }, "");

    element.setAttribute("style", style);

    // Handle pseudo-elements
    ["::before", "::after"].forEach((pseudo) => {
      const pseudoStyle = window.getComputedStyle(element, pseudo);
      const pseudoContent = pseudoStyle.getPropertyValue("content");
      if (pseudoContent && pseudoContent !== "none") {
        const pseudoElement = document.createElement("span");
        pseudoElement.setAttribute(
          "style",
          Array.from(pseudoStyle).reduce((styleString, property) => {
            return `${styleString}${property}:${pseudoStyle.getPropertyValue(property)};`;
          }, ""),
        );
        pseudoElement.textContent = pseudoContent.replace(/['"]/g, ""); // Remove quotes from content
        element.appendChild(pseudoElement);
      }
    });
  });
}
const getPixelColor = async (
  element: SVGElement,
  canvas: HTMLCanvasElement,
  clickX: number,
  clickY: number,
) => {
  inlineStyles(element);
  // Get bounding box dimensions

  // Create an inline SVG that captures the entire element tree
  const svg = new XMLSerializer().serializeToString(element);
  const { width, height, top, left } = element.getBoundingClientRect();

  // Create a Blob and object URL for the SVG
  const blob = new Blob([svg], { type: "image/svg+xml;charset=utf-8" });
  const url = URL.createObjectURL(blob);

  // Create a canvas and an image to render the SVG
  canvas.width = width;
  canvas.height = height;
  const ctx = canvas.getContext("2d") as CanvasRenderingContext2D;
  const img = new Image();
  img.width = 2000;
  img.height = 2000;
  img.crossOrigin = "anonymous";

  // Render the SVG onto the canvas
  return new Promise((resolve) => {
    img.onload = () => {
      document.body.appendChild(img);
      ctx.drawImage(img, 0, 0);
      // // Clean up the object URL
      URL.revokeObjectURL(url);
      const link = document.createElement("a");
      link.download = "rendered-element.svg";
      link.href = canvas.toDataURL("image/svg");
      link.click();

      // Get pixel data at the click position
      const x = clickX - left;
      const y = clickY - top;
      const pixelData = ctx.getImageData(x, y, 1, 1).data;
      resolve(
        `rgba(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]}, ${pixelData[3] / 255})`,
      );
    };
    img.src = url;
  });
};

const HueLights = () => {
  const [value, setValue] = useState<number>(70);
  const colorWheelRef = useRef<SVGSVGElement>(null);
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const [openModal, setOpenModal] = useState<boolean>(true);

  const handleClick = useCallback((event: MouseEvent) => {
    // Get the element under the clicked position
    if (colorWheelRef.current && canvasRef.current) {
      // colorWheelRef.current?.removeEventListener("click", handleClick);
      getPixelColor(
        colorWheelRef.current,
        canvasRef.current,
        event.clientX,
        event.clientY,
      ).then((color) => {
        console.log(`Color at clicked position: ${color}`);
      });
    }
  }, []);


  useEffect(() => {
    if (colorWheelRef.current) {
      colorWheelRef.current.addEventListener("click", handleClick);
    }

    return () => {
      colorWheelRef.current?.removeEventListener("click", handleClick);
    };
  }, [colorWheelRef.current]);

  return (
    <>
      <MobileDisclaimer active={openModal} setActive={setOpenModal} />
      <div className="flex flex-col justify-around items-center rounded-3xl bg-transparent backdrop-blur-[200px] p-6 gap-6">
        <header>
          <h1 className="font-sans text-3xl font-extralight pb-6">
            Meet Phillips Hue
          </h1>
        </header>
        {/* <div className="color-wheel" /> */}
        {/* <svg
          ref={colorWheelRef}
          xmlns="http://www.w3.org/2000/svg"
          width={500}
          height={500}
        >
          <foreignObject width={500} height={500}>
            <div className="clear-all color-wheel" />
          </foreignObject>
        </svg> */}
        <CanvasWheel />
        <div
          style={{ backgroundColor: `hsl(0, 100%, ${value}%)` }}
          className="aspect-square w-20"
        >
        </div>
          {/* <canvas ref={canvasRef}></canvas> */}
        <div className="flex flex-col items-start justify-around gap-10 rounded-3xl bg-black/10 px-10 pb-7 pt-5 backdrop-blur-[1000px]">
          <h3>Main room</h3>
          <RangeSlider value={value} setValue={setValue} width={400} />
          {/* <div className="border-1 h-5 w-10">
          <div className="aspect-square h-5 rounded-full bg-white" />
        </div> */}
        </div>
      </div>
    </>
  );
};

export default HueLights;
