import {
  Dispatch,
  FC,
  MouseEventHandler,
  SetStateAction,
  TouchEventHandler,
  useCallback,
  useEffect,
  useRef,
} from "react";

type conversionValsType = {
  left: number;
  right: number;
  distancePercentage: number;
} | null;

type RangeSliderProps = {
  value?: number;
  setValue?: Dispatch<SetStateAction<number>>;
  width?: number | string;
  sliderSize?: number;
};

const calcPositionPercentage = (
  trackConversionVals: conversionValsType,
  ev: MouseEvent | TouchEvent,
  sliderCenter: number,
) => {
  const eventHints = ["mouse", "click"];

  if (trackConversionVals) {
    const { left, right, distancePercentage } = trackConversionVals;

    let xPos;
    if (eventHints.some((evHint) => ev.type.includes(evHint))) {
      xPos = (ev as MouseEvent).clientX;
    } else {
      xPos = (ev as TouchEvent).touches[0].clientX;
    }

    const clampedPosition =
      xPos < left
        ? left
        : xPos >= right + sliderCenter
          ? right + sliderCenter
          : xPos;
    // bitwise operation | 0 to floor positive numbers
    const percentage =
      ((clampedPosition - left - sliderCenter) / distancePercentage) | 0;
    return percentage;
  } else {
    return 0;
  }
};

const RangeSlider: FC<RangeSliderProps> = ({
  value = 0,
  setValue,
  width = 200,
  sliderSize = 16,
}) => {
  const sliderCenter = sliderSize / 2;
  const sliderTop = sliderSize / 4;

  const handleActiveState = useRef<boolean>(false);
  const leftState = useRef<number>(value);
  const sliderRef = useRef<HTMLDivElement>(null);
  const fillRef = useRef<HTMLDivElement>(null);
  const trackRef = useRef<HTMLDivElement>(null);
  const trackConversionVals = useRef<conversionValsType>(null);

  const initializeTrackConversionVals = useCallback(() => {
    if (trackRef.current && !trackConversionVals.current) {
      const domRect = trackRef.current.getBoundingClientRect();
      const { left, right } = domRect;
      const distancePercentage = (right - left) / 100;
      trackConversionVals.current = { left, right, distancePercentage };
    }
  }, []);

  const handleMouseDown: MouseEventHandler<HTMLDivElement> = useCallback(
    (ev) => {
      ev.stopPropagation();
      if (ev.type.includes("touch")) return;
      initializeTrackConversionVals();
      handleActiveState.current = true;
      document.addEventListener("mousemove", handleMouseMove);
      document.addEventListener("mouseup", handleMouseUp);
    },
    [],
  );

  const handleTouchStart: TouchEventHandler<HTMLDivElement> = useCallback(
    (ev) => {
      ev.stopPropagation();
      if (ev.type.includes("mouse")) return;
      initializeTrackConversionVals();
      handleActiveState.current = true;
      document.addEventListener("touchmove", handleTouchMove);
      document.addEventListener("touchend", handleTouchEnd);
    },
    [],
  );

  const handleMouseUp = useCallback((ev: MouseEvent) => {
    ev.stopPropagation();
    ev.stopImmediatePropagation();
    handleActiveState.current = false;
    document.removeEventListener("mousemove", handleMouseMove);
    document.removeEventListener("mouseup", handleMouseUp);
  }, []);

  const handleTouchEnd = useCallback((ev: TouchEvent) => {
    ev.stopPropagation();
    ev.stopImmediatePropagation();
    handleActiveState.current = false;
    document.removeEventListener("touchmove", handleTouchMove);
    document.removeEventListener("touchend", handleTouchEnd);
  }, []);

  const handleWindowResize = () => {
    trackConversionVals.current = null;
  };

  const handleLeftValAssingment = useCallback(
    (ev: MouseEvent | TouchEvent) => {
      if (sliderRef.current && trackConversionVals.current && fillRef.current) {
        const percentage = calcPositionPercentage(
          trackConversionVals.current,
          ev,
          sliderCenter,
        );
        leftState.current = percentage;
        if (setValue) {
          setValue(Math.max(0, percentage));
        } else {
          sliderRef.current.style.left = `${percentage}%`;
          fillRef.current.style.width = `${percentage}%`;
        }
      }
    },
    [handleActiveState.current],
  );

  const handleMouseMove = useCallback(
    (ev: MouseEvent) => {
      if (handleActiveState.current) {
        handleLeftValAssingment(ev);
      }
    },
    [handleActiveState.current],
  );

  const handleTouchMove = useCallback(
    (ev: TouchEvent) => {
      if (handleActiveState.current) {
        handleLeftValAssingment(ev);
      }
    },
    [handleActiveState.current],
  );

  const handleTrackClick = useCallback(
    (ev: MouseEvent) => {
      const elem = ev.target as HTMLElement;
      if (elem && ["track", "fill"].includes(elem.dataset.c ?? "")) {
        handleLeftValAssingment(ev);
      }
    },
    [trackRef.current],
  );

  useEffect(() => {
    document.addEventListener("resize", handleWindowResize);
    if (trackRef.current) {
      trackRef.current.addEventListener("click", handleTrackClick);
    }

    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
      document.removeEventListener("mouseup", handleMouseUp);
      document.removeEventListener("resize", handleWindowResize);
    };
  }, []);

  return (
    <div
      ref={trackRef}
      data-c="track"
      className="relative z-10 rounded-full bg-black/5 backdrop-blur-3xl"
      style={{ height: sliderCenter, width }}
    >
      <div
        ref={fillRef}
        data-c="fill"
        className="absolute left-0 top-0 z-20 rounded-l-full bg-gradient-to-r from-white/5 to-white"
        style={{
          width: `${Math.max(value + 1, 3)}%`,
          height: sliderCenter,
        }}
      />
      <div
        ref={sliderRef}
        data-c="handle"
        className="absolute -top-[4px] z-20 aspect-square h-4 rounded-full bg-white before:absolute before:content-[''] before:h-16 before:z-5 before:transparent before:-inset-6"
        style={{
          height: sliderSize,
          top: -sliderTop,
          left: `${value}%`,
        }}
        onMouseDown={handleMouseDown}
        onTouchStart={handleTouchStart}
      />
    </div>
  );
};

export default RangeSlider;
