import "./FlightApp.scss";
import "./Plane.scss";
import AirlineLogo from "./AirlineLogo";
import FlightInfoHeadline from "./FlightInfoHeadline";
import AirportInfo, { FlightTime } from "./AirportInfo";
import FlightETA from "./FlightETA";
import SmallPlane, { IconPlane } from "./Plane";
import WorldMap from "./WorldMap";
import MaskPath from "./MaskPath";
import { FC, useCallback, useEffect, useState } from "react";
import emiratesPlane from "./img/emirates-a380.ong.png";
import FlightFeature from "./FlightFeature";
import bgClouds from "./img/bg-clouds.png";
import cloudPassingBy from "./img/clouds-passing-by.png";
import DesignCredit from "../Shared/DesignCredit";

type FlightCardProps = {
  handleClick: () => void;
  toggled?: boolean;
  collapsed?: boolean;
};

const FlightCardCollapsed: FC<FlightCardProps> = ({ handleClick }) => {
  return (
    <div
      id="flight-app"
      className={`relative flex w-11/12 min-w-[300px] max-w-screen-sm cursor-pointer flex-col overflow-hidden rounded-[3rem] bg-slate-100 bg-[radial-gradient(circle_at_top_right,rgba(50,125,255,.5),#FFFFFF00)] px-8 py-6 text-slate-600 subpixel-antialiased drop-shadow-lg transition-all sm:w-9/12`}
      onClick={handleClick}
    >
      <header className="flex gap-3">
        <AirlineLogo color="bg-red-600" name="Emirates" />
        <FlightInfoHeadline flight="FC 340" concourse="5B" />
      </header>
      <main className="relative z-20 mt-[52px]">
        <AirportInfo city="San Francisco" code="sfo" />
      </main>
      <footer className="relative z-20 mt-[18px] flex items-baseline gap-3">
        <FlightETA status="on time" eta="in 17min" />
      </footer>
      <WorldMap />
      <SmallPlane />
    </div>
  );
};

const isMobile = () => {
  return /Android|iPhone|iPad|iPod|Opera Mini|IEMobile|WPDesktop/i.test(
    navigator.userAgent,
  );
};

const FlightCardExpanded: FC<FlightCardProps> = ({ handleClick }) => {
  const userOnMobile = isMobile();

  const FLIGHT_PERCENTAGE = 10;
  // const FLIGHT_PERCENTAGE = 29;

  const [flightPathPosition, setFlightPathPosition] = useState<number>(0);

  useEffect(() => {
    const timeoutIds = [];
    for (let i = 0; i < FLIGHT_PERCENTAGE; i++) {
      timeoutIds.push(
        setTimeout(() => {
          setFlightPathPosition((position) => position + 1);
        }, 30 * i),
      );
    }
  }, []);

  return (
    <div
      id="flight-app"
      className={`absolute flex w-11/12 min-w-[300px] max-w-screen-sm cursor-pointer flex-col overflow-hidden rounded-[3rem] bg-slate-100 bg-gradient-to-b from-blue-700 via-blue-400 to-white px-8 py-6 text-slate-600 subpixel-antialiased drop-shadow-lg transition-all sm:w-9/12`}
      onClick={handleClick}
    >
      <div
        className="absolute inset-0"
        style={{
          backgroundImage: `url(${bgClouds})`,
          backgroundSize: "cover",
          backgroundPositionX: "78%",
          backgroundPositionY: "50px",
          backgroundRepeat: "no-repeat",
          opacity: ".7",
        }}
      ></div>
      <header className="flex gap-3">
        <AirlineLogo color="bg-red-600" name="Emirates" />
        <FlightInfoHeadline
          color="text-gray-100"
          flight="FC 340"
          concourse="5B"
        />
      </header>
      <main className="relative z-20 mt-[52px]">
        <div className="flex w-full justify-between gap-4">
          <AirportInfo
            city="Dubai"
            code="dxb"
            cityColor="text-white"
            codeColor="text-white"
            time="8:50 AM"
            isSmall
          />
          <div className="flex w-full flex-col items-center gap-5">
            <FlightTime time="16hr 5min" isSmall={false} />
            <div className="relative w-full">
              <div
                className="h-1 grow rounded-full bg-white"
                style={!userOnMobile ? { mask: "url(#mask)" } : {}}
              />
              <div
                className={`absolute -top-2`}
                style={{
                  filter: "brightness(2)",
                  left: `${flightPathPosition + 2}%`,
                }}
              >
                {" "}
                <div
                  className={
                    userOnMobile ? "mix mx-2 rounded-full bg-blue-700" : ""
                  }
                >
                  <IconPlane size={20} />
                </div>
              </div>
              {!userOnMobile && (
                <MaskPath size={35} position={flightPathPosition} />
              )}
            </div>
            <FlightETA
              status="on time"
              eta="in 17min"
              etaColor="text-white"
              isColumn
            />
          </div>
          <AirportInfo
            city="San Francisco"
            code="sfo"
            cityColor="text-white"
            codeColor="text-white"
            time="1:50 PM"
            align="end"
            isSmall
          />
        </div>
        <div className="relative mb-20 flex justify-center overflow-visible">
          <img
            src={emiratesPlane}
            className="plane-hovering relative z-10 -ml-8 w-10/12"
          />
          <div
            aria-label="clouds passsing by"
            style={{
              backgroundImage: `url(${cloudPassingBy})`,
            }}
            className="cloud-flying-animation-front -bottom-30 absolute -left-10 z-20 w-[120%] overflow-visible"
          >
            <div style={{ height: "400px" }}></div>
          </div>
        </div>
      </main>
      <footer className="bottom-0 left-0 z-20 flex w-full flex-col justify-center pt-4">
        <div className="item-end mb-2 flex w-full justify-around">
          <FlightFeature label="Class" feature="Business" />
          <FlightFeature label="Gate" feature="12C" />
          <FlightFeature label="Passengers" feature="1 Adult" />
          <FlightFeature label="Luggage" feature="2" />
        </div>
      </footer>
      <div
        aria-label="clouds passsing by"
        style={{
          backgroundImage: `url(${cloudPassingBy})`,
          backgroundSize: "200%",
          backgroundPositionX: "0%",
        }}
        className="cloud-flying-animation absolute bottom-0 left-0 w-[120%] overflow-visible"
      >
        <div style={{ height: "450px" }}></div>
      </div>
    </div>
  );
};

const FlightApp = () => {
  const [collapsed, setCollapsed] = useState<boolean>(true);
  const [toggled, setToggled] = useState<boolean>(false);

  const handleClick = useCallback(() => {
    let locked = false;
    if (!locked) {
      locked = true;
      setToggled((t) => !t);
      setTimeout(() => {
        setCollapsed((c) => !c);
        locked = false;
      }, 20);
    }
  }, []);

  return (
    <>
      {collapsed ? (
        <FlightCardExpanded
          toggled={toggled}
          handleClick={handleClick}
          collapsed={collapsed}
        />
      ) : (
        <>
          <FlightCardCollapsed
            toggled={toggled}
            handleClick={handleClick}
            collapsed={collapsed}
          />
          <DesignCredit
            name="Emmanuel"
            link="https://handheld.design/design/flight-interactive-widget"
          />
        </>
      )}
    </>
  );
};

export default FlightApp;
