import { FC } from "react";

type FlightETAProps = {
  status: "on time" | "late";
  eta: string;
  isColumn?: boolean;
  etaColor?: `text-${string}`;
};

const FlightETA: FC<FlightETAProps> = ({
  status,
  eta,
  isColumn,
  etaColor = "text-gray-500/75",
}) => {
  return (
    <div
      className={`flex w-fit ${isColumn ? "flex-col items-center gap-1" : "items-baseline gap-3"}`}
      style={{ filter: "hue-rotate(20deg)" }}
    >
      <div
        aria-label="flight-status"
        className="flex h-fit w-fit grow-0 gap-2 rounded-full bg-gradient-to-b from-green-400 to-teal-600 px-3 py-1 font-semibold text-white whitespace-nowrap"
      >
        <span className="inline-block -rotate-45">↓</span>
        {status}
      </div>
      <p aria-label="flight ETA" className={`font-extrabold  ${etaColor}`}>
        {eta}
      </p>
    </div>
  );
};

export default FlightETA;
