type AILocalCardProps = {
  childrenMain: ReactNode;
  childrenFooter: ReactNode;
  ratio?: "aspect-square" | "aspect-video";
  noPaddingMain?: boolean;
  noPaddingFooter?: boolean;
};

type AILocalCardContentProps = {
  children: ReactNode;
  noPadding?: boolean;
};

const AILocalCard: FC<AILocalCardProps> = ({
  childrenMain,
  childrenFooter,
  ratio,
  noPaddingMain,
  noPaddingFooter,
}) => {
  const classes = clsx(
    "aspect-auto md:aspect-video flex h-fit flex-col divide-y divide-gray-400/10 rounded-xl bg-gradient-to-br from-black/70 via-gray-800/5 to-black/60 ring-2 ring-white/5 drop-shadow-md backdrop-blur-[200px] md:max-h-[500px] lg:max-h-[400px]",
    { 
      'lg:aspect-square': ratio === 'aspect-square', 
      'lg:aspect-video': ratio === 'aspect-video', 
    },
  );

  return (
    <div className={classes}>
      <AILocalCardMain noPadding={noPaddingMain}>
        {childrenMain}
      </AILocalCardMain>
      <AILocalCardFooter noPadding={noPaddingFooter}>
        {childrenFooter}
      </AILocalCardFooter>
    </div>
  );
};

const AILocalCardMain: FC<AILocalCardContentProps> = ({
  children,
  noPadding,
}) => {
  return noPadding ? (
    <>{children}</>
  ) : (
    <div className={"h-[80%] px-8 py-6"}>{children}</div>
  );
};
const AILocalCardFooter: FC<AILocalCardContentProps> = ({ children }) => {
  return (
    <div className="grow-0 overflow-hidden text-ellipsis px-8 py-6">
      {children}
    </div>
  );
};

