type PointDataType = {
  value: number;
  delta: number;
  percentChange: number;
};

type PointsDataProps = {
  pointA: PointDataType;
  pointB: PointDataType;
};

const formatCurrency = (val: number, noCents?: boolean) =>
  new Intl.NumberFormat("en-US", {
    style: "currency",
    currency: "USD",
    ...(noCents ? { minimumFractionDigits: 0 } : {}),
  }).format(val);

const formatNumber = (val: number) => new Intl.NumberFormat().format(val);

const formatPercentage = (val: number) =>
  new Intl.NumberFormat("en-US", {
    style: "percent",
    minimumFractionDigits: 1,
  }).format(val);

const getSign = (val: number) => (val === 0 ? "" : val < 0 ? "-" : "+");

interface PointDataProps extends PointDataType {
  color: string;
}

const PointData: FC<PointDataProps> = ({
  value,
  delta,
  percentChange,
  color,
}) => {
  return (
    <div
      className="flex items-end justify-between gap-6 border-l border-solid pl-2"
      style={{ borderLeft: `${color} 1px solid` }}
    >
      <div className="flex flex-col leading-none">
        <span className="text-white/80">
          {formatCurrency(Math.abs(value), true)}
        </span>{" "}
        <br /> {getSign(percentChange)}
        {formatNumber(delta)}
      </div>
      <div className="flex h-full items-end" style={{ color: `${color}` }}>
        {getSign(percentChange)}
        {formatPercentage(percentChange)}
      </div>
    </div>
  );
};

const PointsData: FC<PointsDataProps> = ({ pointA, pointB }) => {
  const { value: valueA, delta: deltaA, percentChange: pCA } = pointA;
  const { value: valueB, delta: deltaB, percentChange: pCB } = pointB;

  return (
    <div className="absolute left-[40%] top-[10px] z-40 rounded-xl bg-white/5 p-1 px-4 pb-2 text-[.75rem] backdrop-blur-[100px] md:top-[50px] lg:top-[100px]">
      <h3 className="text-[.9rem] font-normal text-white">Mar 21, 2024</h3>
      <div className="mt-2 flex flex-col items-end gap-2 divide-gray-500">
        <PointData
          value={valueA}
          delta={deltaA}
          percentChange={pCA}
          color={"rgba(0,150,200,.9)"}
        />
        <hr className="w-full border border-dashed border-red-400" />
        <PointData
          value={valueB}
          delta={deltaB}
          percentChange={pCB}
          color={"rgba(250,140,140,.9)"}
        />
      </div>
    </div>
  );
};

