import { FC } from "react";

export const FlightTime = ({
  time,
  isSmall = true,
}: {
  time: string;
  isSmall?: boolean;
}) => {
  return (
    <h3 className={`w-fit ${isSmall ? "text-sm" : "text-lg"} text-white`}>
      {time}
    </h3>
  );
};

type AirportInfoProps = {
  city: string;
  code: `${string}${string}${string}`;
  codeColor?: `text-${string}`;
  cityColor?: `text-${string}`;
  isSmall?: boolean;
  time?: string;
  align?: "start" | "end";
};

const AirportInfo: FC<AirportInfoProps> = ({
  city,
  cityColor = "text-gray-500/80",
  code,
  codeColor = "text-slate-900",
  isSmall = false,
  time,
  align = "start",
}) => {
  return (
    <div className={`flex flex-col items-${align} w-fit`}>
      <h1
        aria-label="City"
        className={`${cityColor} ${isSmall ? "text-[.7rem]" : "text-lg"}`}
      >
        {city}
      </h1>
      <h2
        arial-label="Airport code"
        className={`font-round isSmall-y-90 tracking-[-.1rem] ${codeColor} ${!isSmall ? "airport-code text-[4rem] leading-[4.5rem]" : "text-[2.8rem] leading-[3.1rem]"}`}
      >
        {isSmall ? (
          code.toUpperCase()
        ) : (
          <>
            {code[0].toUpperCase()}
            <span className="tracking-[-.35rem]">{code[1].toUpperCase()}</span>
            {code[2].toUpperCase()}
          </>
        )}
      </h2>
      {time && (
        <div className="mt-2">
          <FlightTime isSmall={isSmall} time={time} />
        </div>
      )}
    </div>
  );
};

export default AirportInfo;
