import {
  FC,
  MouseEventHandler,
  useCallback,
  useEffect,
  useRef,
  useState,
} from "react";
import colorWheelImg from "./img/color-wheel.webp";

type CanvasLightProps = {
  x: number;
  y: number;
  color: string;
};

const CanvasLight: FC<CanvasLightProps> = ({ x, y, color }) => {
  const size = 40;
  const offset = size / 3;

  const [coords] = useState<Partial<CanvasLightProps>>({
    x: x - size / 2,
    y: y - size / 2 - offset,
  });
  const [bgColor] = useState<string>(color);

  return (
    <div
      style={{
        backgroundColor: "transparent",
        top: coords.y,
        left: coords.x,
      }}
      className="absolute z-20 h-10 w-10"
    >
      <svg
        className="overflow-visible"
        width={size}
        height={size}
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d={`M ${size / 2},${size / 1.45} L ${size},${size / 8} A ${size / 2} ${size / 2} 0 1 0 ${size / 8},${size / 8} L${size / 2},${40 / 1.45}`}
          fill={bgColor}
          stroke="white"
          strokeWidth="2.5"
          strokeLinejoin="round"
        />
      </svg>
    </div>
  );
};

const CanvasWheel = () => {
  const canvasRef = useRef<HTMLCanvasElement>(null);
  const imageContext = useRef<CanvasRenderingContext2D | null>(null);

  const [lights, setLights] = useState<CanvasLightProps[]>([]);

  const handleClick: MouseEventHandler<HTMLCanvasElement> = useCallback(
    (ev) => {
      const x = ev.clientX;
      const y = ev.clientY;

      if (canvasRef.current) {
        const domRect = canvasRef.current.getBoundingClientRect() as DOMRect;

        const posLeft = x - domRect.left;
        const posTop = y - domRect.top;

        if (!imageContext.current) {
          imageContext.current = canvasRef.current.getContext("2d", {
            willReadFrequently: true,
          });
        }
        const pixelData = imageContext.current?.getImageData(
          posLeft,
          posTop,
          1,
          1,
        ).data;
        if (!pixelData) return;
        const color = `rgba(${pixelData[0]}, ${pixelData[1]}, ${pixelData[2]}, ${pixelData[3] / 255})`;
        setLights((lights) => [...lights, { x: posLeft, y: posTop, color }]);
        return color;
      }
    },
    [canvasRef.current],
  );

  useEffect(() => {
    if (canvasRef.current) {
      if (!imageContext.current) {
        imageContext.current = canvasRef.current.getContext("2d", {
          willReadFrequently: true,
        });
      }
      const img = new Image();
      img.onload = () => {
        imageContext.current?.drawImage(img, 0, 0, 300, 300);
      };
      img.src = colorWheelImg;
    }
  }, []);

  return (
    <div className="relative h-fit w-fit">
      <canvas
        onClick={handleClick}
        width={300}
        height={300}
        className="relative"
        ref={canvasRef}
      >
        No canvas support available
      </canvas>
      {lights.map(({ x, y, color }) => (
        <CanvasLight key={color + x + y} x={x} y={y} color={color} />
      ))}
    </div>
  );
};

export default CanvasWheel;
