import "./App.scss";
import {
  ComponentType,
  Dispatch,
  SetStateAction,
  useEffect,
  useMemo,
  useState,
} from "react";
import Pagination from "./components/Pagination";
import Cards from "./components/Cards";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  useParams,
  useNavigate,
} from "react-router-dom";
import { getSlug } from "./utils";

function App() {
  const [pageIndex, setPageIndex] = useState<number>(1);
  const memoCards = useMemo(
    () => Cards.map((Card: ComponentType) => Card),
    [Cards],
  );
  const slugs = memoCards.map((c) => getSlug(c.name ?? c.displayName));
  const CardComponent = memoCards[pageIndex - 1];

  useEffect(() => {
    if (window.location.hash.includes('#')) {
      const newHash = window.location.hash.replace('#', '');
      if (window.location.hash !== `#${newHash}`) {
        window.location.hash = newHash;
      }
    }
  }, [pageIndex]);

  return (
    <Router  basename="/cards">
      <div
        style={{
          fontFeatureSettings:
            '"ss01" 1, "cv11" 1, "cv13" 1, "calt" 1, "ccmp" 1, "locl" 1, "kern" 1',
        }}
        className="relative"
      >
        <Pagination pageCount={Cards.length} slugs={slugs} currentPage={pageIndex} />
        <CardComponent />
      </div>
      <Routes>
        <Route
          path="/:slug"
          element={
            <SlugHandle slugs={slugs ?? []} setPageIndex={setPageIndex} />
          }
        ></Route>
        <Route
          path="/"
          element={
            <SlugHandle slugs={slugs ?? []} setPageIndex={setPageIndex} />
          }
        ></Route>
      </Routes>
    </Router>
  );
}

const SlugHandle = ({
  slugs,
  setPageIndex,
}: {
  slugs: (string | undefined)[];
  setPageIndex: Dispatch<SetStateAction<number>>;
}) => {
  const { slug } = useParams<{ slug: string }>();
  const sanitizedSlud = slug;
  if(!slug){
    console.log('asdfasd'.repeat(30), JSON.parse(JSON.stringify(window.location)))
  }
  const slugIndex = slugs.indexOf(sanitizedSlud);
  const pageIndexNumber = slugIndex + 1;
  // console.log({slugIndex, sanitizedSlud, pageIndexNumber})
  const navigate = useNavigate();

  useEffect(() => {
    if(window.location.href.includes('#')){
      navigate(window.location.hash.replace('#',''))
      return
    }
    if(slugIndex < 0){
      setPageIndex(1)
    }
    else if (sanitizedSlud) {
      if (pageIndexNumber >= 0) {
        setPageIndex(pageIndexNumber);
      }
    }
  }, [slug, pageIndexNumber, sanitizedSlud]);
  return <></>;
};

export default App;
